<template>
  <div id="productModal"
    class="modal fade"
    tabindex="-1"
    role="dialog"
    aria-labelledby="productModal"
    aria-hidden="true">
    <div class="modal-dialog modal-xl modal-dialog-scrollable" role="document">
      <div class="modal-content border-0">
        <div class="modal-header bg-info text-white">
          <h5 class="modal-title">{{ modalMode === mode.new ? '' : '' }}</h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-sm-4">
              <div class="form-group" v-for="n in 5" :key="'imageUrl' + n">
                <label :for="'imageUrl' + n">{{ ' ' + n }}</label>
                <input type="text"
                  :id="'imageUrl' + n"
                  v-model="formData.imageUrl[n - 1]"
                  class="form-control"
                  placeholder="">
              </div>

              <div class="form-group">
                <label for="fileUpload">
                   
                  <i v-if="fileUploading" class="fas fa-spinner fa-spin"></i>
                </label>
                <input id="fileUpload"
                  ref="file"
                  type="file"
                  class="form-control"
                  @change="uploadFile">
              </div>

              <img class="img-fluid" :src="formData.imageUrl[0]">
            </div>
            <div class="col-sm-8">
              <div class="form-group">
                <label for="title"></label>
                <input type="text"
                  id="title"
                  v-model="formData.title"
                  class="form-control"
                  placeholder="">
              </div>
              <div class="form-row">
                <div class="form-group col-md-6">
                  <label for="category"></label>
                  <input type="text"
                    id="category"
                    v-model="formData.category"
                    class="form-control"
                    placeholder="">
                </div>
                <div class="form-group col-md-6">
                  <label for="unit"></label>
                  <input type="text"
                    id="unit"
                    v-model="formData.unit"
                    class="form-control"
                    placeholder="">
                </div>
              </div>
              <div class="form-row">
                <div class="form-group col-md-6">
                  <label for="origin_price"></label>
                  <input type="number"
                    id="origin_price"
                    v-model="formData.origin_price"
                    class="form-control"
                    placeholder="">
                </div>
                <div class="form-group col-md-6">
                  <label for="price"></label>
                  <input type="number"
                    id="price"
                    v-model="formData.price"
                    class="form-control"
                    placeholder="">
                </div>
              </div>
              <div class="form-group">
                <label for="description"></label>
                <textarea id="description"
                  v-model="formData.description"
                  class="form-control"
                  placeholder="">
                </textarea>
              </div>
              <div class="form-group">
                <label for="content"></label>
                <textarea id="content"
                  v-model="formData.content"
                  class="form-control"
                  placeholder="">
                </textarea>
              </div>
              <div class="form-group" v-if="formData.options">
                <label for="stars"></label>
                <input type="number"
                  id="stars"
                  v-model="formData.options.stars"
                  min="0"
                  max="5"
                  class="form-control"
                  placeholder="">
              </div>
              <div class="form-group">
                <div class="form-check">
                  <input type="checkbox"
                    id="enabled"
                    class="form-check-input"
                    v-model="formData.enabled">
                  <label for="enabled" class="form-check-label"></label>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-dismiss="modal"></button>
          <button type="button" class="btn btn-primary" @click="submitProduct"></button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['modalMode'],
  data() {
    return {
      apiInfo: {
        forSingleProduct: '/admin/ec/product',
        forFileUpload: '/admin/storage',
      },
      mode: {
        new: 0,
        edit: 1,
      },
      cleanData: {
        id: 0,
        title: '',
        category: '',
        content: '',
        description: '',
        imageUrl: [],
        enabled: true,
        origin_price: 0,
        price: 0,
        unit: '',
        options: {
          stars: 0,
        },
      },
      formData: {
        id: 0,
        title: '',
        category: '',
        content: '',
        description: '',
        imageUrl: [],
        enabled: true,
        origin_price: 0,
        price: 0,
        unit: '',
        options: {
          stars: 0,
        },
      },
      fileUploading: false,
    };
  },
  methods: {
    clearFormData() {
      this.formData = JSON.parse(JSON.stringify(this.cleanData));
    },
    async prepareProduct(id) {
      try {
        const result = await this.axios.get(`${this.apiInfo.forSingleProduct}/${id}`);
        this.formData = result.data.data;
        this.formData.options = JSON.parse(result.data.data.options);
      } catch (err) {
        console.log(err);
      }
    },
    submitProduct() {
      if (this.modalMode === this.mode.new) {
        this.newProduct();
      } else {
        this.editProduct();
      }
    },
    newProduct() {
      this.formData.options = JSON.stringify(this.formData.options);
      this.$emit('set-loading', true);
      this.axios.post(this.apiInfo.forSingleProduct, this.formData).then(() => {
        this.$emit('set-loading', false);
        this.$emit('refresh-product-list');
        this.hideSelf();
      }).catch((err) => {
        this.$emit('set-loading', false);
        console.log(err);
      });
    },
    editProduct() {
      this.formData.options = JSON.stringify(this.formData.options);
      this.$emit('set-loading', true);
      this.axios.patch(`${this.apiInfo.forSingleProduct}/${this.formData.id}`, this.formData).then(() => {
        this.$emit('set-loading', false);
        this.$emit('refresh-product-list');
        this.hideSelf();
      }).catch((err) => {
        this.$emit('set-loading', false);
        console.log(err);
      });
    },
    hideSelf() {
      $('#productModal').modal('hide');
    },
    uploadFile() {
      if (this.$refs.file.files.length === 0) {
        return;
      }

      this.fileUploading = true;

      const fileReference = this.$refs.file.files[0];
      const form = new FormData();
      form.append('file', fileReference);
      this.axios.post(this.apiInfo.forFileUpload, form, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      }).then((res) => {
        this.fileUploading = false;
        if (res.status === 200) {
          this.formData.imageUrl.push(res.data.data.path);
        }
      }).catch(() => {
        console.log(' 2 MB');
        this.fileUploading = false;
      });
    },
  },
};
</script>
